import { CangjieInputEvent, Controller } from '@ali/4ever-cangjie';
import { createSpaceHandler } from '@ali/4ever-utils';

export default function createOnCangjieInput() {
  const onSubSpace = createSpaceHandler({
    mark: { type: 'vertAlign', data: { value: 'subscript' } },
    delimiter: [{ character: '~', size: 1 }],
  });
  const onSupSpace = createSpaceHandler({
    mark: { type: 'vertAlign', data: { value: 'superscript' } },
    delimiter: [{ character: '^', size: 1 }],
  });

  return function onCangjieInput(
    event: CangjieInputEvent,
    controller: Controller,
    next: Function,
  ) {
    if (event.detail.data !== ' ') {
      return next();
    }
    return onSubSpace(event, controller, () => {
      onSupSpace(event, controller, next);
    });
  };
}
